@import '../base.scss';

.c-contact{
  position: relative;
  min-height: t-to-rem(820);
  max-width: t-to-rem(2000);
  margin: auto;

  &__top-container{
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 0;
    &-inner{
      
      margin: t-to-rem(110) auto 0;
      text-align: center;
    }

    &:after {
      content: '';
      position: absolute;
      background-color: $s-color--background-tertiary;
      top: 0;
      bottom: 0;
      left: -100%;
      right: -100%;
      z-index: -1;
    }
  }

  &__drawer{
    transform: translateX(100%);
    opacity: 0;
    width: 100%;
    transition: all $s-anim--time-med $s-anim--ease-in-out;    
    overflow: hidden;
    display: flex;
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    z-index: $s-z--modal;
    
    &.is-open{
      transform: translateX(0);
      opacity: 1;
    }


    &-background{
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      z-index: -1;
      object-fit: cover;
      height: 100%;
    }

    &-inner{
      margin: t-to-rem(17);
      background-color: rgba($s-color--background, .2);
      padding: t-to-rem(60) t-to-rem(22) t-to-rem(22);
      backdrop-filter: blur(t-to-rem(32));
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
    }

    &-close{
      position: absolute;
      top: t-to-rem(19);
      right: t-to-rem(22);
      @include t-width-and-height(12);
      display: block;
    }

    &-pre-title{
      @include t-font--heading-7;
      color: $s-color--font-6;
      margin-bottom: t-to-rem(18);
    }
    &-title{
      color: $s-color--font-6;
      margin-bottom: t-to-rem(46);
    }
  }

  &__bottom-container{
    padding-top: t-to-rem(20);
    margin-left: $s-gutter--mobile;
    margin-right: $s-gutter--mobile;
  }
  &__open-form-button{
    cursor: pointer;
  }

  &__form{
    display: flex;
    flex-direction: column;
    flex: 1;
    &-field.is-textarea{
      margin-bottom: t-to-rem(56);
    }
    &-submit{
      &.o-btn{
        display: block;
        width: 100%;
        max-width: t-to-rem(295);
        margin: auto auto 0;
      }
    }
    &-success{
      color: $s-color--font-6;
    }
  }

  &__pre-title{
    margin-bottom: t-to-rem(20);
  }

  &__title{
    color: $s-color--font-6;
    margin-bottom: t-to-rem(38);
  }

  &__text{
    text-align: center;
    color: rgba($s-color--font-1, .6);
  }

  &__info{
    padding: t-to-rem(30) 0;
    &-row {
      display: flex;
      justify-content: space-between;
      padding: t-to-rem(10) 0;
    }
    &-value{
      color: $s-color--font-8;
    }

  }

  &__double-image{
    position: relative;
    margin: auto;
    &-background{
      height: 100%;
    }
    &-overlay{
      width: auto;
      margin: auto;
      height: 100%;
      &-container{
        @include t-absolute-center-x-y;
        height: 70%;
        overflow: hidden;
      }
    }
  }

  &__contact-button{
    white-space: nowrap;
  }
  
  @include t-media-query($s-small-up) {
    
    &__bottom-container{
      margin-left: $s-gutter--small;
      margin-right: $s-gutter--small;

    }
  }

  @include t-media-query($s-medium-down) {
    &__main-container{
      margin: t-to-rem(40) 0;
    }
    &__text{      
      margin: 0 auto t-to-rem(36);
      max-width: t-to-rem(300);
      &.is-desktop{display: none;}
    }
    &__double-image{
      max-width: 400px;
      margin: auto;
      margin-bottom: t-to-rem(-207);
      &.is-desktop{display: none;}
    }
    &__top-container{
      margin-bottom: t-to-rem(207);
    }

    &__info{    
      border-top: 1px solid rgba(black, 0.1);
      border-bottom: 1px solid rgba(black, 0.1);
    }
  }
  
  @include t-media-query($s-medium-up) {
    &__top-container{
      position: relative;
      z-index: 0;
      &-inner{
        text-align: left;
        margin-top: t-to-rem(208);
        margin-left: $s-gutter--medium;
        margin-bottom: t-to-rem(85);
        width: 34.3%;
        margin-right: auto;
      }
    }
    &__bottom-container{
      width: 34.3%;
      margin-right: auto;
      margin-left: $s-gutter--medium;
      padding-top: t-to-rem(60);
      padding-bottom: t-to-rem(60);
    }

    &__drawer,
    &__double-image{
      position: absolute;
      top: t-to-rem(208);
      right: 0;
      bottom: 0;
    }
    &__drawer{
      width: 43.6%;

    }
    &__double-image{
      max-width: 43.6%;
    }

    &__double-image{
      &.is-mobile{display: none;}
    }

    

    &__title{
      white-space: nowrap;
    }

    &__text{
      text-align: left;
      width: t-to-rem(416);
      &.is-mobile{display: none;}
    }
  }
  
  @include t-media-query($s-large-up) {
    &__bottom-container{
      padding-top: t-to-rem(80);
      padding-bottom: t-to-rem(80);
    }
  }
  
  @include t-media-query($s-xxlarge-up) {
    &__top-container-inner{
      margin-left: $s-gutter--xxlarge;
    }
    &__bottom-container{
      margin-left: $s-gutter--xxlarge;
    }
  }
}